* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Quicksand", sans-serif;
  color: white;
  background-color: rgb(235, 235, 235);
  font-weight: 300;
}

hr {
  margin: 10px 0;
}

.searchbar {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  margin-left: 2rem;
  font-size: 1rem;
  padding: 0.5rem;
}

input,
.submit {
  background-color: white;
  border: 0;
  font-size: 1rem;
  padding: 0.5rem;
}

input:focus,
.submit:focus {
  outline: none;
}

.app {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.weather-container {
  background-size: 100%;
  background-repeat: no-repeat;
  margin-top: 0.5rem;
  width: 70%;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.day {
  background-image: url("./images/day.jpg");
  background-size: 100% 100%;
}

.night {
  background-image: url("./images/night.jpg");
  background-size: 100% 100%;
}

.current-weather {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}

.temperature {
  display: flex;
  justify-content: center;
  width: 70%;
}

.temperature span {
  font-size: 60px;
  margin-top: 10px;
}

.location {
  font-size: 20px;
  margin-bottom: 5px;
}

.additional-info {
  width: 90%;
}

.additional-first,
.additional-second {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.min-max {
  width: 12%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.humidity-wind {
  display: flex;
  flex-direction: column;
}

.forecast-container {
  width: 30%;
}

.forecast-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.forecast-day {
  width: 34%;
}

.forecast-min-max {
  width: 20%;
  display: flex;
  flex: row;
  justify-content: space-evenly;
}

.forecast-item img {
  margin-right: 2rem;
}

.degree-units {
  cursor: pointer;
}

@media (max-width: 1000px) {
  .day {
    background-image: linear-gradient(to right, #283c86, #45a247);
  }

  .night {
    background-image: repeating-linear-gradient(
      to right,
      #0f2027,
      #203a43,
      #2c5364
    );
  }

  .current-weather {
    width: 90%;
  }

  .temperature {
    align-items: center;
    width: 70%;
  }

  .temperature span {
    font-size: 42px;
  }

  .location {
    font-size: 18px;
    margin-bottom: 5px;
  }

  .min-max {
    width: 60px;
  }

  .humidity-wind {
    height: 45px;
    justify-content: space-between;
  }

  .forecast-container {
    width: 80%;
  }

  .searchbar {
    margin-left: 0;
  }

  .weather-container {
    margin-top: 0.5rem;
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 1000px) and (min-height: 730px) {
  .weather-container {
    justify-content: space-evenly;
  }
}
